  justify-content: space-between;
  border-bottom: 2px solid #ffffff;
  margin-bottom: 5%;
}
.transport__line img {
  width: 25%;
  height: 50px;
}
.transport__line .hamburger-menu {
  display: none;
}
@media (max-width: 1369px) {
  .transport__line .hamburger-menu {
    display: block;
  }
}
.transport__line #menu__toggle {
  opacity: 0;
}
.transport__line #menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}
.transport__line #menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}
.transport__line #menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}
.transport__line #menu__toggle:checked ~ .menu__box {
  visibility: visible;
  right: 10%;
  top: 10%;
  width: 20%;
  padding: 5px 2px 5px 2px;
  background-color: #ffffff;
  border-radius: 10px;
}
.transport__line .menu__btn {
  display: flex;
  align-items: center;
  position: relative;
  bottom: 5%;
  left: 90%;
  width: 26px;
  height: 26px;
  cursor: pointer;
  z-index: 1;
}
.transport__line .menu__btn > span,
.transport__line .menu__btn > span::before,
.transport__line .menu__btn > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #ccc6c6;
  transition-duration: 0.25s;
}
.transport__line .menu__btn > span::before {
  content: "";
  top: -8px;
}
.transport__line .menu__btn > span::after {
  content: "";
  top: 8px;
}
.transport__line .menu__box {
  display: block;
  position: absolute;
  visibility: hidden;
  top: 0;
  width: 300px;
  margin: 0;
  padding: 80px 0;
  list-style: none;
  background-color: #ECEFF1;
  box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
  transition-duration: 0.25s;
}
.transport__line .menu__item {
  display: block;
  padding: 12px 20px;
  color: #333;
  font-size: 10px;
  font-weight: 400;
  text-decoration: none;
  transition-duration: 0.2s;
}
.transport__line .menu__item:hover {
  background-color: #CFD8DC;
}

